.recipe{
    border-radius: 10px;
    box-shadow: 0px 5px 20px black;
    margin:20px;
    display:flex;
    flex-direction:column;
    justify-content:space-around;
    background:white;
    align-items:center;
    min-width:40%;
    padding: 20px;
    flex:1;
}

.image{
    border-radius: 50%;
    width: 100px;
    height: 100px;
}

.text{
    font-size: 50px;
    color:#243949;
    font-family: 'Pattaya', sans-serif;
}