.App{
  min-height: 100vh;
  background-image: linear-gradient(to right, #243949 0%, #517fa4 100%);

}

h2{
  text-align: center;
  color: white;
  font-size: 40px;
  font-family: 'Pattaya', sans-serif;
}

.search-form{
  min-height:10vh;
  display:flex;
  justify-content:center;
  align-items:center;
}

.search-bar{
  width:50%;
  border:none;
  padding:10px;
  border-radius: 10px;
}

.search-button{
  background: lightcoral;
  border:none;
  padding:10px 20px;
  color:white;
  margin-left: 5px;
  border-radius: 10px;
}

.recipes{
  display:flex;
  justify-content:space-around;
  flex-wrap: wrap;
}

